<template>
  <div id="detail">
    <detail-nav-bar @itemClick="titleClick" :current-index="currentIndex"></detail-nav-bar>
    <scroll
      :probe-type="3"
      ref="scroll"
      class="content"
      @scroll="contentScroll"
      :data="[topImages, goods, shopInfo, detailInfo, goodsParam,commentInfo,recommendList ]"
    >
      <detail-swiper ref="base" :images="topImages"></detail-swiper>
      <detail-base-info :goods="goods"></detail-base-info>
      <detail-shop-info :shop-info="shopInfo"></detail-shop-info>
      <detail-goods-info :detail-info="detailInfo"></detail-goods-info>
      <detail-param-info ref="param" :goods-param="goodsParam"></detail-param-info>
      <detail-comment-info ref="comment" :comment-info="commentInfo"></detail-comment-info>
      <detail-recommend-info ref="recommend" :recommend-list="recommendList"></detail-recommend-info>
    </scroll>
    <back-top v-show="showBackTopDetail" @click.native="backTop"></back-top>
    <detail-bottom-dar @addToast="addToast"></detail-bottom-dar>
    <!-- Toast  -->
    <!-- <toast ref="toast"></toast> -->
  </div>
</template>
<script>

// Scroll: better-scroll
import Scroll from "components/common/scroll/Scroll";
// DetailNavBar: 
import DetailNavBar from "./childComps/DetailNavBar";
// DetailSwiper: 
import DetailSwiper from "./childComps/DetailSwiper";
// DetailBaseInfo: ,
import DetailBaseInfo from "./childComps/DetailBaseInfo";
// DetailShopInfo: 
import DetailShopInfo from "./childComps/DetailShopInfo";
// DetailGoodsInfo: ,,
import DetailGoodsInfo from "./childComps/DetailGoodsInfo";
// DetailParamInfo: 
import DetailParamInfo from "./childComps/DetailParamInfo";
// DetailCommentInfo: 
import DetailCommentInfo from "./childComps/DetailCommentInfo";
// DetailRecommendInfo: 
import DetailRecommendInfo from "./childComps/DetailRecommendInfo";
// DetailBottomDar: 
import DetailBottomDar from "./childComps/DetailBottomBar";
// backTop: 
import { backTopItem } from "common/mixins/mixins.js";
// map -> 
import {mapActions} from 'vuex'
// Toast()
/**
 *  Toast 
 *  */
// import Toast from "components/common/toast/Toast";
import * as Change from "common/const.js";
import {
  getDetail,
  getRecommend,
  Goods,
  ShopInfo,
  GoodsParam
} from "network/detail";

export default {
  name: "Detail",
  data() {
    return {
      iid: "",
      itemInfo: {},
      shopInfo: {},
      columns: {},
      topImages: [],
      goods: {},
      shopInfo: {},
      detailInfo: {},
      goodsParam: {},
      commentInfo: {},
      recommendList: [],
      themeTops: [],
      showBackTopDetail: false,
      currentIndex: 0
    };
  },
  components: {
    DetailNavBar,
    DetailSwiper,
    DetailBaseInfo,
    DetailShopInfo,
    DetailGoodsInfo,
    DetailParamInfo,
    DetailCommentInfo,
    DetailRecommendInfo,
    Scroll,
    DetailBottomDar
    /**
     *  Toast 
     *  */
    // Toast
  },
  mixins: [backTopItem],
  // created: 
  created() {
    this._getDetail();
    this._getRecommend();
  },
  // updated :
  updated() {
    this._getOffsetTops();
  },
  methods: {
    ...mapActions(['addCart']),

    // 
    addToast() {
      // 1.
      let obj = {};
      // 2.
      // id
      obj.iid = this.iid;
      // 
      obj.topImages = this.itemInfo.topImages[0];
      // 
      obj.title = this.itemInfo.title;
      // 
      obj.desc = this.itemInfo.desc;
      // 
      obj.price = this.itemInfo.highNowPrice;
      // 3.(store)
      this.addCart(obj).then(res => {
        console.log(obj,'ogj')
        /**
         *  toast 
         *  */
        // this.$refs.toast.message = res;
        // this.$refs.toast.isShow = true;

        // // 
        // setTimeout(() => {
        //   this.$refs.toast.isShow = false;
        // }, 1000);

        /**
         *  Toast 
         *  */
        this.$toast(res);
      });
    },
    
    _getOffsetTops() {
      this.themeTops = [];
      this.themeTops.push(this.$refs.base.$el.offsetTop);
      this.themeTops.push(this.$refs.param.$el.offsetTop);
      this.themeTops.push(this.$refs.comment.$el.offsetTop);
      this.themeTops.push(this.$refs.recommend.$el.offsetTop);
      // : themeTops
      this.themeTops.push(Number.MAX_VALUE);
    },
    contentScroll(position) {
      // 
      this.showBackTopDetail = position.y <= -Change.TOP_DISTANCE;

      // 
      this._listenScrollTheme(-position.y);
    },
    _listenScrollTheme(position) {
      for (let i = 0, length = this.themeTops.length; i < length; i++) {
        let iPos = this.themeTops[i];
        //   if (
        //     (i < length - 1 &&
        //       position >= iPos &&
        //       position < this.themeTops[i + 1]) ||
        //     (i === length - 1 && position >= iPos)
        //   ) {
        //     this.currentIndex = i;
        //   }
        /**
         * :
         *  :
         *    : (i < (length-1) && position >= iPos && position < this.themeTops[i+1]) || (i === (length-1) && position >= iPos)
         *    : , 
         *    : , 
         *  :
         *    : themeTops, top.
         *    : , 
         *    : int
         * : , (position >= iPos)?
         * : position0, break, i.
         */
        if (position >= iPos && position < this.themeTops[i + 1]) {
          if (this.currentIndex !== i) {
            this.currentIndex = i;
          }
          break;
        }
      }
    },
    // 
    backTop() {
      this.$refs.scroll.scrollTo(0, 0, 1000);
    },
    _getDetail() {
      // 1.iid
      this.iid = this.$route.params.iid;
      getDetail(this.iid).then(res => {
        // result: 
        const data = res.result;
        // itemInfo: 
        this.itemInfo = data.itemInfo;
        // shopInfo: 
        this.shopInfo = data.shopInfo;
        // columns: 
        this.columns = data.columns;
        //  img
        this.topImages = this.itemInfo.topImages;

        // 
        this.goods = new Goods(
          this.itemInfo,
          this.columns,
          this.shopInfo.services
        );

        // 
        this.shopInfo = new ShopInfo(this.shopInfo);

        // detailInfo: 
        this.detailInfo = data.detailInfo;

        this.goodsParam = new GoodsParam(
          data.itemParams.info,
          data.itemParams.rule
        );

        if (data.rate.list) {
          this.commentInfo = data.rate.list[0];
        }
      });
    },
    _getRecommend() {
      getRecommend().then((res, error) => {
        if (error) return;
        this.recommendList = res.data.list;
      });
    },
    titleClick(index) {
      // $nextTick: 
      this.$nextTick(() => {
        this.$refs.scroll.scrollTo(0, -this.themeTops[index], 600);
      });
    }
  }
};
</script>

<style scopen>
.content {
  position: absolute;
  top: 44px;
  bottom: 0px;
  bottom: 60px;
  z-index: 10;
}
</style>

